<template>    
<div class="menu-icon-bar">
    <div class="material-icons menu-icons " v-for="item in copy" v-bind:class="item.id2" >{{item.title2}}</div>
    <select class="selector font-family-selector ">
    <option v-for="option in options" class="section" :value="option">{{option}}</option>
    </select>
    <select  class="selector font-size-selector">    
     <option  v-for="num in numbers" :value="num"  >{{num[0]+num[1]}}</option>
    </select>
    <div class="material-icons menu-icon  style-icon" v-for="post in posts" v-bind:class="post.id" >{{post.title}}</div>   
    <div class="material-icons menu-icon align-icon " v-for="post1 in posts1" v-bind:class="post1.id1" >{{post1.title1}}</div>
    <div class="menu-icon icon-color-fill">
     <input  class="color-picker background-color-picker" type="color" value="#fffff" />
     <div class="material-icons color-fill-icon " >format_color_fill</div>
    </div>
    <div class="menu-icon icon-color-text">
     <input  class="color-picker text-color-picker"  type="color" value="black"/>
     <div class="material-icons color-fill-text">format_color_text</div>
    </div>
    <span class="material-icons download" curser="pointer">
     cloud_download
     </span>
     <span class="material-icons open">
     cloud_upload
      </span>
      
        <button class="savedb" >Save</button>
        <button class="opendb" >Open</button>
    
 </div>
</template>
<script>
export default{
    iconbar:"Iconbar",
   data(){
    return{
      copy: [
      { id2: 'icon-cut', title2: 'content_cut' },
      { id2: 'icon-copy', title2: 'content_copy' },
      { id2: 'icon-paste', title2: 'content_paste' }
    ],

        posts1: [
      { id1: 'icon-align-left', title1: 'format_align_left' },
      { id1: 'icon-align-center', title1: 'format_align_center' },
      { id1: 'icon-align-right', title1: 'format_align_right' }
    ],
        posts: [
      { id: 'icon-bold', title: 'format_bold' },
      { id: 'icon-italic', title: 'format_italic' },
      { id: 'icon-underline', title: 'format_underline' }
    ],
        numbers:['10px','12px','14px','16px','18px','20px','22px','24px','26p','28px','30px','32px'],
        options:['Noto Sans','Arial','Calibri','Comic Sans MS',"Courier New","Impact","Garamond","Lato","Open Sans","Palatino","Verdana"]
    };
   }
}

</script>
<style>
.section{
    font-family: v-bind('options');
}

</style>

